import { FC, useEffect, useMemo, useRef, useState } from 'react';
import * as ST from './styled';
import { useAuth } from '@providers/AuthProvider';
import {
  AppLoader,
  Avatar,
  AvatarSize,
  Button,
  ButtonVariant,
  InfoItem,
  Input,
  Role,
  SideModal
} from '@src/components';
import { toastError, toastSuccess } from '@src/utils/toast';
import { IconSize } from '@components/Icon';
import { UsersApi } from '@src/api/Users/UsersApi';
import { EditPassword } from '@components/ProfileModal/EditPassword';
import { EditInfo } from '@components/ProfileModal/EditInfo';

interface IProps {
  onClose: () => void;
}

export const ProfileModal: FC<IProps> = ({ onClose }) => {
  const [isEdit, setIsEdit] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const { getMe, user, setAvatar } = useAuth();
  const inputRef = useRef<HTMLInputElement | null>(null);

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const file = e.target.files[0];

      if (!file) {
        return;
      }

      const formData = new FormData();
      formData.append('photo', file);

      try {
        const response = await UsersApi.uploadAvatar(formData);
        setAvatar(response.data.url);
        toastSuccess(response.data.message)
      } catch (error) {
        console.error(error);
      }
    }
  };

  const handleUpload = () => {
    inputRef.current?.click();
  };

  useEffect(() => {
    loadUser();
  }, []);

  const loadUser = async () => {
    try {
      setIsLoading(true);
      await getMe()
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false)
    }
  }

  const renderedContent = useMemo(() => {
    if (isLoading) {
      return <AppLoader/>;
    }

    if (isEdit) {
      return (
        <>
          <EditPassword />
          <EditInfo firstName={user?.firstName} lastName={user?.lastName} refetch={loadUser} />
          <ST.Footer>
            <Button fullWidth variant={ButtonVariant.OUTLINE} text="" onClick={() => setIsEdit(false)} />
          </ST.Footer>
        </>
      )
    }

    return (
      <>
        <ST.HeaderInfo>
          <ST.AvatarContainer onClick={handleUpload}>
            <input
              ref={inputRef}
              type="file"
              onChange={handleFileChange}
              accept="image/png, image/gif, image/jpeg"
              hidden
            />
            <Avatar
              firstName={user?.firstName}
              lastName={user?.lastName}
              src={user?.avatar}
              userId={user?.id}
              size={AvatarSize.EXTRA}
              figure={'square'}
            />
            <ST.PlusIcon
              type="plus"
              size={IconSize.XXXL}
            />
          </ST.AvatarContainer>
          <ST.Name>{user?.lastName} {user?.firstName}</ST.Name>
        </ST.HeaderInfo>
        <InfoItem
          text=":"
          info={(
            <ST.Roles>
              {user?.roles.map(it => (
                <Role key={it.id} role={it.value} />
              ))}
            </ST.Roles>
          )}
        />
        <InfoItem
          text=":"
          info={<ST.Email>{user?.email}</ST.Email>}
        />
        <ST.Footer>
          <Button text="" fullWidth onClick={() => setIsEdit(true)} />
        </ST.Footer>
      </>
    );
  }, [isEdit, isLoading, user]);

  return (
    <SideModal
      titleText=""
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Wrapper>
        {renderedContent}
      </ST.Wrapper>
    </SideModal>
  )
}
