import { FC, useState } from 'react';
import * as ST from './styled';
import { Button, ButtonVariant, Input } from '@src/components';
import { useFormik } from 'formik';
import { UsersApi } from '@src/api/Users/UsersApi';
import { handleFormError, toastError, toastSuccess } from '@src/utils/toast';
import * as yup from 'yup';

interface IInfoForm {
  firstName: string;
  lastName: string;
}

let VALIDATION_SCHEMA = yup.object().shape({
  firstName: yup.string().required(' '),
  lastName: yup.string().required(' '),
});

interface IProps {
  firstName?: string;
  lastName?: string;
  refetch: () => void;
}

export const EditInfo: FC<IProps> = ({ firstName, lastName, refetch }) => {
  const [isLoading, setIsLoading] = useState(false);

  const form = useFormik<IInfoForm>({
    initialValues: {
      firstName: firstName || '',
      lastName: lastName || '',
    },
    validationSchema: VALIDATION_SCHEMA,
    onSubmit: async (values, { resetForm }) => {
      try {
        setIsLoading(true);
        const res = await UsersApi.changeInfo(values);
        resetForm();
        toastSuccess(res.data.message);
        refetch();
      } catch (e) {
        handleFormError(e, form);
      } finally {
        setIsLoading(false);
      }
    }
  })

  return (
    <ST.Form onSubmit={form.handleSubmit}>
      <ST.FormTitle>   </ST.FormTitle>
      <ST.FormContent>
        <Input
          name="lastName"
          value={form.values.lastName}
          onChange={form.handleChange}
          onBlur={form.handleBlur}
          placeholder=" "
          caption=""
          isError={Boolean(form.errors.lastName)}
          errorText={form.errors.lastName}
        />
        <Input
          name="firstName"
          value={form.values.firstName}
          onChange={form.handleChange}
          onBlur={form.handleBlur}
          placeholder=" "
          caption=""
          isError={Boolean(form.errors.firstName)}
          errorText={form.errors.firstName}
        />
        <Button fullWidth variant={ButtonVariant.FILLED} text="" isLoading={isLoading}/>
      </ST.FormContent>
    </ST.Form>
  );
}
