import { toast } from 'react-toastify';
import { useFormik } from 'formik';
import { FormikValues } from 'formik/dist/types';
import { AxiosError } from 'axios';

export const toastError = (e: unknown) => {
  toast((e as AxiosError<IApiError>).response?.data.message, {
    type: 'error',
  })
}

export const toastSuccess = (message: string) => {
  toast(message, {
    type: 'success',
  })
}

interface IApiError {
  errors: {
    errors: Array<{
      location: string
      msg: string
      param: string
      value: string
    }>
  },
  message: string;
}

export const handleFormError = <Values extends FormikValues = FormikValues>(e: unknown, form: ReturnType<typeof useFormik<Values>>) => {
  const errors = (e as AxiosError<IApiError>).response?.data.errors?.errors;

  if (errors) {
    errors.forEach(it => {
      form.setFieldError(it.param, it.msg);
    });
  }
  toastError(e);
}
