import { FC, useState } from 'react';
import * as ST from './styled';
import { Button, ButtonVariant, Input } from '@src/components';
import { useFormik } from 'formik';
import { UsersApi } from '@src/api/Users/UsersApi';
import { handleFormError, toastError, toastSuccess } from '@src/utils/toast';
import * as yup from 'yup';

interface IChangePasswordForm {
  password: string;
  repeatPassword: string;
  newPassword: string;
}

let VALIDATION_SCHEMA = yup.object().shape({
  password: yup.string().required(' '),
  newPassword: yup.string().required(' '),
  repeatPassword: yup.string()
    .required(' ')
    .oneOf(
      [yup.ref('newPassword')],
      '  ',
    ),
});

export const EditPassword: FC = () => {
  const [isLoading, setIsLoading] = useState(false);

  const form = useFormik<IChangePasswordForm>({
    initialValues: {
      newPassword: '',
      password: '',
      repeatPassword: '',
    },
    validationSchema: VALIDATION_SCHEMA,
    onSubmit: async (values, { resetForm }) => {
      try {
        setIsLoading(true);
        const res = await UsersApi.changePassword(values);
        resetForm();
        toastSuccess(res.data.message);
      } catch (e) {
        handleFormError(e, form);
      } finally {
        setIsLoading(false);
      }
    }
  })

  return (
    <ST.Form onSubmit={form.handleSubmit}>
      <ST.FormTitle> </ST.FormTitle>
      <ST.FormContent>
        <Input
          type="password"
          name="password"
          value={form.values.password}
          onChange={form.handleChange}
          onBlur={form.handleBlur}
          placeholder="  "
          caption=" "
          isError={Boolean(form.errors.password)}
          errorText={form.errors.password}
        />
        <Input
          type="password"
          name="newPassword"
          value={form.values.newPassword}
          onChange={form.handleChange}
          onBlur={form.handleBlur}
          placeholder="  "
          caption=" "
          isError={Boolean(form.errors.newPassword)}
          errorText={form.errors.newPassword}
        />
        <Input
          type="password"
          name="repeatPassword"
          value={form.values.repeatPassword}
          onChange={form.handleChange}
          onBlur={form.handleBlur}
          placeholder="  "
          caption="  "
          isError={Boolean(form.errors.repeatPassword)}
          errorText={form.errors.repeatPassword}
        />
        <Button fullWidth variant={ButtonVariant.FILLED} text="" isLoading={isLoading}/>
      </ST.FormContent>
    </ST.Form>
  );
}
